* {
    text-decoration: none;
}

.navbar {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f2f2f2;
    justify-content: space-evenly;
    color: black;
    padding: 7px;
    justify-content: space-around;
}

.navbar img {
    margin-top: 15px;
    margin-right: 7px;
}

@media only screen and (min-width: 630px) {
    body {
        background-color: white;
    }
}

.buttonsNavbar {
    box-sizing: border-box;
    margin-top: 15px;
}

.btnOne {
    position: relative;
    color: black;
}

.btnOne:hover {
    color: green;
}

.btnOne::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 0.175rem;
    background: green;
    bottom: -5px;
    left: 0;
}

.btnCenter::after {
    transform: scale(0, 1);
    transform-origin: 0% 100%;
    transition: transform 0.3s ease;
}

.btnCenter:hover::after {
    transform: scale(1, 1);
}